import linIconButton from './lin-icon-button.vue';
export default {
	props: {
		title: {
			type: [String, Boolean],
			default: false
		},
		fixed: {
			type: Boolean,
			default: true
		},
		noreadnum: {
			type: Number,
			default: 0
		}
	},
	components: {
		linIconButton
	},
	data() {
		return {
			statusBarHeight: 0,
			navBarHeight: 0
		}
	},
	mounted() {



		this.navBarHeight = this.statusBarHeight + uni.upx2px(90)
	},
	computed: {
		fixedStyle() {
			return `height:${this.navBarHeight}px`
		},
		getTitle() {
			let noreadnum = this.noreadnum > 0 ? '(' + this.noreadnum + ')' : ''
			return this.title + noreadnum
		}
	},
	methods: {
		search(){
			console.log("search");
		},
		openExtend() {
			this.$emit('openExtend')
		}
	},
}
